<template>
  <form @submit.prevent="addTodo()" class="createAppContainer">
    <input type="text" placeholder="Create a new to-do..." v-model="newTodo" class="createApp" />
  </form>
</template>

<script>
export default {
  data() {
    return {
      newTodo: "",
      newtime: "",
    };
  },
  methods: {
    addTodo() {
      // this.newtime =
      if (this.newTodo.length > 0) {
        this.newtime = new Date();
        this.newtime = (this.newtime.getMonth()+1)+'/'+this.newtime.getDate()+' '+this.newtime.getHours()+':'+this.newtime.getMinutes()
        this.$emit("on-new-todo", this.newTodo, this.newtime);
      }
      this.newTodo = "";
      this.newtime = "";
    },
  },
};
</script>

<style scope lang="scss">
.createAppContainer {
  width: 100%;
  margin-bottom: 20px;
  input:focus {
    background-color: #e6e6ea;
  }
  .createApp {
    width: 100%;
    padding: 8px 16px;
    font-size: 14px;
    color: #555;
    border: 1px solid #e9e9ed;
    border-radius: 4px;
    background-color: #f0f0f4;
    outline: none;
  }
}
</style>