<template>
  <li class="todoContainer" v-show="!completed">
    <!-- <input type="checkbox" class="box"> -->
    <button @click="$emit('on-toggle')" v-if="!isEditing" :class="{completed}" class="main">
      <span>{{description}}</span>
    </button>
    <!-- finishEditing -->
    <form v-else @submit="finishEditing" class="main">
      <input
        type="text"
        v-model="newTodoDescription"
        ref="newTodo"
        @blur="finishEditing"
        class="input"
      />
    </form>
    <!-- startEditing -->
    <button @click="startEditing" class="editButton">Edit</button>
    <!-- $on-delete -->
    <button @click="$emit('on-delete')" class="deleteButton" >Delete</button>
  </li>
</template>

<script>
export default {
  props: {
    description: String,
    completed: Boolean,
  },
  data() {
    return {
      isEditing: false,
      newTodoDescription: "",
    };
  },
  methods: {
    startEditing() {
      if (this.isEditing) {

        this.finishEditing();
      } else {
        this.newTodoDescription = this.description;
        this.isEditing = true;
        this.update();
        this.$nextTick(() => this.$refs.newTodo.focus());
      }
    },
    finishEditing() {
      this.isEditing = false;
      this.$emit("on-edit", this.newTodoDescription);
    },
    update(){
      this.$emit('update',this);
      // console.log(this);
    }
  },
};
</script>

