<template>
  <div id="app">
    <div class="container">
      <div class="top">
        <div class="titleContainer">
          <h1>{{title}}</h1>
        </div>
        <div class="inputContainer">
          <create-todo @on-new-todo="addTodo(arguments)"></create-todo>
        </div>
      </div>
      <div class="outer-container">
        <div class="inner-container">
          <div class="todolistTitle">
            <h3>Upcoming</h3>
          </div>
          <div class="todoListContainer">
            <div>
              <ul>
                <todo
                  v-for="(todo, index) in todos"
                  :key="index"
                  :description="todo.description"
                  :completed="todo.completed"
                  @on-delete="deleteTodo(todo)"
                  @on-edit="editTodo(todo,$event)"
                  @on-toggle="toggleTodo(todo)"
                  @update="updateDetail(todo)"
                ></todo>
              </ul>
            </div>
          </div>
          <div class="completedTitle">
            <h3>Completed</h3>
            <ul>
              <finish
                v-for="(todo, index) in todos"
                :key="index"
                :description="todo.description"
                :completed="todo.completed"
                @on-delete="deleteTodo(todo)"
                @on-edit="editTodo(todo,$event)"
                @on-toggle="toggleTodo(todo)"
                @update="updateDetail(todo)"
              ></finish>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="detailContainer">
      <detail :currently="currently" v-if="changeDetail"></detail>
      <div v-else class="newlook">
        <div>
          <h3>编辑任务查看详情</h3>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Todo from "./Todo";
import Finish from "./Finish";
import CreateTodo from "./CreateTodo";
import Detail from "./Detail";

export default {
  props: {
    title: String,
  },
  data() {
    return {
      changeDetail: false,
      todos: [
        //Do the dishes Take out the trash Finish doing laundry
        {
          description: "step1:请在输入框发布新项目",
          completed: false,
          time: "",
          detail: "",
        },
        {
          description: "step2:点击右侧按钮进行编辑或删除操作",
          completed: false,
          time: "",
          detail: "删除属于不可逆操作",
        },

        {
          description: "step3:右侧界面可以编辑当前项目详情",
          completed: false,
          time: "",
          detail: "右上角显示项目创建时间（前往查看新创建的项目详情）",
        },
        {
          description: "step4:点击我以完成项目",
          completed: false,
          time: "",
          detail: "当完成状态的项目再次点击时会取消完成状态",
        },
        {
          description: "step5:完成的项目会在这边显示",
          completed: true,
          time: "",
          detail: "处于完成状态的项目，详情页title会高亮显示",
        },
      ],
      currently: {},
    };
  },
  methods: {
    addTodo(newdata) {
      this.todos.push({
        description: newdata[0],
        time: newdata[1],
        completed: false,
      });
    },
    deleteTodo(deleteTodo) {
      // alert('删除成功 '+'( '+deleteTodo.description+" )")
      this.todos = this.todos.filter((todo) => todo !== deleteTodo);
      this.currently = {};
      this.changeDetail = false;
    },
    editTodo(todo, newTodoDescription) {
      todo.description = newTodoDescription;
    },
    toggleTodo(todo) {
      todo.completed = !todo.completed;
      // 完成和代办分开数组储存
      // if (todo.completed) {
      //   this.completeds.push(todo);
      //   this.deleteTodo(todo);
      // } else {
      //   this.todos.push(todo);
      //   this.deleteTodo(todo);
      // }
    },
    updateDetail(detailObejct) {
      this.currently = detailObejct;
      this.changeDetail = true;
    },
  },
  components: {
    CreateTodo,
    Todo,
    Detail,
    Finish,
  },
};
</script>

